import React, { Component } from "react";

export class Section2 extends Component {
  render() {
    return (
      <div className="section section2">
        <div className="section-title section-title-side">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">01</h6>
          </div>
          <h5>
            How it <span>works</span>
          </h5>
          <h6>speed-up your business</h6>
          <p>
            DevOps aligns your development team so they can deploy new software just as fast as you do business. But, our DevOps as a
            Service is so much more than our proprietary platform. <br /> <br /> At HyperOps, we thoroughly analyze your existing workflow
            and infrastructure, customize and integrate all of the tools your team needs, and then manage your migration. With our platform
            and continuous support, your team has the best environment to work faster, more efficiently, and with fewer failures.
          </p>
        </div>
        <div className="graph-image"></div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1"></div>
            <div className="list-item-text">
              <h6>Easy access</h6>
              <p>
                Access our DevOps as a Service with just a few clicks via our dedicated web platform. We will outfit it with all the
                infrastructure and bespoke technical adjustments you need.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2"></div>
            <div className="list-item-text">
              <h6>Integrations</h6>
              <p>Easily integrate the workflow and task management applications that your team prefers in our one-stop-shop.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3"></div>
            <div className="list-item-text">
              <h6>CI/CD </h6>
              <p>
                We’ve already pre-integrated the best CI/CD practices and processes so you can use one out of the box right away, or build
                it to fit your needs.
              </p>
            </div>
          </div>

          <div className="list-item">
            <div className="list-item-logo list-item-logo4"></div>
            <div className="list-item-text">
              <h6>Users</h6>
              <p>
                Manage your team's access, permissions, and scope. Plus, every action your team performs is logged for clear oversight and
                full transparency.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo5"></div>
            <div className="list-item-text">
              <h6>Environments</h6>
              <p>
                Fully customize your development, staging UAT, and production environments to your needs. Whether they’re identical or
                individualized, you can access them all within a single sign-on.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo6"></div>
            <div className="list-item-text">
              <h6>Infrastructure </h6>
              <p>
                Unlike public clouds where you have no real support or have to purchase it separately, at HyperOps, you have access to ready
                to use servers, networks, Kubernetes clusters, backups, and HA solutions with 99.99% uptime SLA and full support included.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section2;
