import React, { Component } from "react";

export class Section4 extends Component {
  render() {
    return (
      <div className="section section4">
        <div className="section-title section-title-center">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">04</h6>
            <div className="line line2"></div>
          </div>
          <h5>Everything you need </h5>
          {/* <h6>what you need</h6> */}
          <p>to improve collaboration, monitoring, management, and reporting</p>
        </div>
        <div className="list-logo">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1"></div>
            <div className="list-item-text">
              <p>Instant access to DevOps professionals</p>
              <p>from the start and on-call when you need us.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2"></div>
            <div className="list-item-text">
              <p>Expertise, technical documentation, and mentorship</p>
              <p>to help your in-house team continually develop and improve.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3"></div>
            <div className="list-item-text">
              <p>Fine-tuned workflow deployment </p>
              <p>to streamline your code transitions into intuitive, quickly-executed tasks.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4"></div>
            <div className="list-item-text">
              <p>New and improved processes</p>
              <p>extending your internal DevOps capacity while enabling our resources and practices.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo5"></div>
            <div className="list-item-text">
              <p>Exciting infrastructure capabilities</p>
              <p>with full auto-scaling power and optimized resource utilization.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo6"></div>
            <div className="list-item-text">
              <p>24x7x365 support </p>
              <p>provided by our DevOps team of experts.</p>
            </div>
          </div>
        </div>
        <div className="horizontal-line"></div>
        {/* Info container */}
        <div className="info-container">
          <div className="text-left">
            <p className="font-large">
              Compared to other cloud-based DevOps solutions DaaS has the same features and equal functionalities, they are just implemented
              by different tools.
            </p>
            <div className="text-arrow">
              <div className="text">Main differences:</div>
              <div className="arrow"></div>
            </div>
          </div>
          <div className="list">
            <div className="list-item">
              <div className="list-item-logo"></div>
              <div className="list-item-text">
                <p>
                  DaaS provides a fully built and "ready to use" platform. Customers connect directly to projects/repositories. While in
                  other platforms, customers have to build all infrastructure from separate components and after this integrate them. This
                  is not only time consuming, but also requires wide competencies and qualifications.
                </p>
              </div>
            </div>
            <div className="list-item">
              <div className="list-item-logo"></div>
              <div className="list-item-text">
                <p>AI-based monitoring, usage predictions and recommendations on how to optimize resources' allocation.</p>
              </div>
            </div>
            <div className="list-item">
              <div className="list-item-logo"></div>
              <div className="list-item-text">
                <p>DaaS is fully managed and supported service.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section4;
