import React, { Component } from "react";
import $ from "jquery";

export class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.scrollToSection = this.scrollToSection.bind(this);
  }

  scrollToSection(element) {
    const projectsOffset = $("." + element).offset().top - 90;
    $("html,body").animate({ scrollTop: projectsOffset }, "slow");
  }

  render() {
    return (
      <div className="footer section">
        <h6 className="title">HyperOps</h6>

        <div className="row">
          <div className="column">
            <p>
              HyperOps offers know-how IT solutions and DevOps services with exclusive 24/7/365 coverage. For more than 15 years company is
              serving the most demanding financial, telco, media, and e-commerce enterprises keeping a strong track record and one of the
              fastest response times within the market.
            </p>
          </div>
          <div className="column">
            <div className="link" onClick={() => this.scrollToSection("section2")}>
              Advantages
            </div>
            <div className="link" onClick={() => this.scrollToSection("section3")}>
              Back-end
            </div>
            <div
              className="link"
              // style={{ color: "red" }}
              onClick={() => this.scrollToSection("section4")}
            >
              Values
            </div>
            <div
              className="link"
              // style={{ color: "red" }}
              onClick={() => this.scrollToSection("section5")}
            >
              Components
            </div>
          </div>
          <div className="column">
            <div className="link" onClick={() => this.scrollToSection("section6")}>
              Migration plan
            </div>
            <div className="link" onClick={() => this.scrollToSection("section-clients")}>
              Our clients
            </div>
            <div className="link" onClick={() => this.scrollToSection("section8")}>
              Packages
            </div>
            <div className="link" onClick={() => this.scrollToSection("section4b")}>
              Demo version
            </div>
          </div>
          <div className="column column-numbers">
            <div className="link">T. +370 678 03330</div>
            <div className="link">E. devops@hyperops.net</div>
          </div>
          <div className="column">
            <a className="link" href="https://www.facebook.com/HyperOps">
              Facebook
            </a>
            <a className="link" href="https://www.linkedin.com/company/metasite-hyperops/">
              Linkedin
            </a>
          </div>
        </div>
        <div className="horizontal-line"></div>
        <div className="copyright">
          All right reserver <span>@HyperOps</span> - 2020
        </div>
      </div>
    );
  }
}

export default Footer;
