import React, { useState } from "react";
import emailjs from "emailjs-com";

function Section7() {
  function sendEmail(e) {
    e.preventDefault();
    console.log(e.target);
    emailjs.sendForm("email", "template_ji6ru88", e.target, "user_tnk5I7i4z3Pzm0d2B5QxQ").then(
      (result) => {},
      (error) => {}
    );
    e.target.reset();
    setMessageSent(!messageSent);
  }

  const [messageSent, setMessageSent] = useState(false);

  return (
    <div className="section section7 section-clients">
      <div className="section-title section-title-side">
        <div className="icon-numbering">
          <div className="line line1"></div>
          <h6 className="number">07</h6>
        </div>
        <h5>We stand by our clients and they stand by us</h5>
        <p>
          Since 2004, HyperOps has served more than 120+ clients and logged more than 300k working hours. We are also proud to say that 99%
          of potential clients who test our services stay with us long term.
        </p>
      </div>
      <div className="text-box text-box1">
        <div className="qoute-image"></div>
        <div className="overflow-wrapper">
          <div className="text-carousel">
            <div className="text-qoute">
              <p>
                "Our web applications provide time-sensitive information to our global financial industry customers, and as such, they have
                to meet the highest requirements for availability and security at all times. HyperOps team’s technical assistance and
                knowledge of our business logic are invaluable and their dedication to keeping systems running 24×7 is unmatched.""
              </p>
              <p className="name">Karl Mallalieu</p>
              <p>Managing Director, Bond Radar Limited, London, UK</p>
              <div className="lines-image">
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
              </div>
            </div>
            <div className="text-qoute">
              <p>
                "We are extremely happy with the quality of the services and the high technical standards provided by Metasite HyperOps as
                well as their responsiveness in the day-to-day business.""
              </p>
              <p className="name">Dr. Jochen Kraatz</p>
              <p> Managing Director, ARENSIA GmbH, Dusseldorf, Germany</p>
              <div className="lines-image">
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
              </div>
            </div>
            <div className="text-qoute">
              <p>
                "Their incident reaction times were almost always under 10 minutes. I can confidently recommend HyperOps to anyone in need
                of the flawless operation of their critical systems. We are very happy with our relationship and the level of services
                HyperOps provides"
              </p>
              <p className="name">Algirdas Petrauskas</p>
              <p>CTO, Beta.lt + Grupinis.lt, Vilnius, Lithuania</p>
              <div className="lines-image">
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
              </div>
            </div>
            <div className="text-qoute">
              <p>
                "Our web applications provide time-sensitive information to our global financial industry customers, and as such, they have
                to meet the highest requirements for availability and security at all times. HyperOps team’s technical assistance and
                knowledge of our business logic are invaluable and their dedication to keeping systems running 24×7 is unmatched.""
              </p>
              <p className="name">Karl Mallalieu</p>
              <p>Managing Director, Bond Radar Limited, London, UK</p>
              <div className="lines-image">
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="text-box text-box2">
        <p>Join our customers and give our DevOps as a Service a try today.</p>
        <p>
          Once you see how we can change your workflow, you won't want to go back. Enter your email address below to sign up for your free
          demo.
        </p>
      </div>
      <form className="email-container" onSubmit={sendEmail}>
        <div className="input-form">
          <input className="input" placeholder="Your email address" name="email" type="email" required />
          <p className="email-sent" style={{ opacity: messageSent ? "1" : "0" }}>
            Email sent successfully
          </p>
        </div>
        <input className="btn btn-round" type="submit" value="Get demo account" />
      </form>
    </div>
  );
}

export default Section7;
