import React, { Component } from "react";
import $ from "jquery";

export class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      burgerClicked: false,
      browserName: ""
    };
    this.logoClicked = this.logoClicked.bind(this);
    this.handleClickBurger = this.handleClickBurger.bind(this);
    this.scrollToSection = this.scrollToSection.bind(this);
    this.burgerAndScrollFunctions = this.burgerAndScrollFunctions.bind(this);

    this.myRef = React.createRef();
  }

  logoClicked() {
    console.log("gera");
  }

  handleClickBurger() {
    const currentBurgerMenuState = this.state.burgerClicked;
    this.setState({ burgerClicked: !currentBurgerMenuState });
  }

  scrollToSection(element) {
    const projectsOffset = $("." + element).offset().top - 90;
    $("html,body").animate({ scrollTop: projectsOffset }, "slow");
  }

  burgerAndScrollFunctions(element) {
    if (this.state.burgerClicked) {
      this.handleClickBurger();
    }
    this.scrollToSection(element);
  }

  componentDidMount() {
    let browserName = "";
    if (navigator.vendor.match(/google/i)) {
      browserName = "chrome/blink";
    } else if (navigator.vendor.match(/apple/i)) {
      browserName = "safari/webkit";
    } else if (navigator.userAgent.match(/firefox\//i)) {
      browserName = "firefox/gecko";
    } else if (navigator.userAgent.match(/edge\//i)) {
      browserName = "edge/edgehtml";
    } else if (navigator.userAgent.match(/trident\//i)) {
      browserName = "ie/trident";
    } else {
      browserName = navigator.userAgent + "\n" + navigator.vendor;
    }

    this.setState({ browserName: browserName });
  }

  render() {
    let burgerClassName = this.state.burgerClicked ? "burger-menu-clicked" : null;

    return (
      <div className="header">
        <div
          className="navbar"
          style={{ height: window.innerWidth <= 450 && this.state.browserName === "safari/webkit" ? "8rem" : "5rem" }}
        >
          <div className="container-left  container">
            <div className="logo-container" onClick={() => this.burgerAndScrollFunctions("section1")}>
              <div className="logo-image"></div>
              {/* <div className="logo-text">HyperOps</div>
              <div className="logo-text">DevOps</div> */}
            </div>
            <div className="btn btn-how-it-works" onClick={() => this.scrollToSection("section2")}>
              How it works
            </div>
            <div className="btn btn-about" onClick={() => this.scrollToSection("section2b")}>
              The product
            </div>
            <div className="btn btn-benefits" onClick={() => this.scrollToSection("section4")}>
              Benefits
            </div>
            <div className="btn btn-tools" onClick={() => this.scrollToSection("section8")}>
              Packages
            </div>
          </div>
          <div className="container-right container">
            <div className="btn btn-round btn-hollow btn-demo" onClick={() => this.scrollToSection("section7")}>
              Demo
            </div>
            <div className="btn btn-round  btn-contact" onClick={() => this.scrollToSection("footer")}>
              Contact Us
            </div>
          </div>
          <div className={"burger-menu " + burgerClassName} onClick={this.handleClickBurger}>
            <div className="line"></div>
            <div className="line"></div>
          </div>
        </div>
        <div className="sidebar" style={{ transform: this.state.burgerClicked ? "translateX(0%)" : "translateX(-100%)" }}>
          <div className="btn btn-about btn-menu" onClick={() => this.burgerAndScrollFunctions("section2")}>
            About
          </div>
          <div className="btn btn-tools btn-menu" onClick={() => this.burgerAndScrollFunctions("section5")}>
            Tools
          </div>
          <div className="btn btn-benefits btn-menu" onClick={() => this.burgerAndScrollFunctions("section4")}>
            Benefits
          </div>
          <div className="btn btn-how-it-works btn-menu" onClick={() => this.burgerAndScrollFunctions("section3")}>
            How it works
          </div>
          <div className="container">
            <div className="btn btn-round btn-hollow btn-demo" onClick={() => this.burgerAndScrollFunctions("section7")}>
              Demo
            </div>
            <div className="btn btn-round  btn-contact" onClick={() => this.burgerAndScrollFunctions("footer")}>
              Contact Us
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
