import React, { Component } from "react";

export class Section6 extends Component {
  render() {
    return (
      <div className="section section6">
        <div className="section-title section-title-center">
          <div className="icon-numbering">
            <div className="line line1"></div>
            <h6 className="number">06</h6>
            <div className="line line2"></div>
          </div>
          <h5>We’re setting you up for success</h5>
        </div>
        <div className="list">
          <div className="list-item">
            <div className="list-item-logo list-item-logo1">1</div>
            <div className="list-item-text">
              <h6>Infrastructure analysis</h6>
              <p>
                Our team will first examine your current workflow and capacities so we can maximize your transition into DevOps as a
                Service.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo2">2</div>
            <div className="list-item-text">
              <h6>Consultation </h6>
              <p>Together, we’ll come up with a comprehensive migration plan for your infrastructure, people, and processes.</p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo3">3</div>
            <div className="list-item-text">
              <h6>Migration plan defined </h6>
              <p>
                Together we will define migration plan to give you a clear view and understanding what and when will be done and how this
                will impact your operations.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4">4</div>
            <div className="list-item-text">
              <h6> Migration</h6>
              <p>
                Our experts will make sure it’s a smooth transition, implementing CI/CD, load balancing, autoscaling, DR, backup, and
                monitoring solutions for you.
              </p>
            </div>
          </div>
          <div className="list-item">
            <div className="list-item-logo list-item-logo4">5</div>
            <div className="list-item-text">
              <h6> Going live</h6>
              <p>
                Once we deploy the data and code, you’re on board. Now, your team can focus and enjoy the benefits of DevOps as a Service,
                continually improving development, deployment, and maintenance.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section6;
